.apollo {
  color: #34495e; }
  .apollo p {
    letter-spacing: 1px !important;
    line-height: 1.7 !important;
    min-height: 1em !important;
    -webkit-box-sizing: border-box !important;
    box-sizing: border-box !important;
    word-wrap: break-word !important;
    text-align: justify;
    margin: 20px 0 !important; }
  .apollo blockquote {
    border-left: 4px solid #42b983;
    padding: 0 15px !important;
    margin: 0px; }
  .apollo strong {
    font-weight: bold;
    /*border-bottom: 2px solid #BADA55;*/
    /*color: #898989;*/ }
  .apollo body {
    font-size: 15px; }
  .apollo pre {
    background-color: #f8f8f8;
    border-radius: 3px;
    word-wrap: break-word;
    padding: 12px 13px;
    font-size: 13px; }
  .apollo h1,
  .apollo h2,
  .apollo h3,
  .apollo h4,
  .apollo h5,
  .apollo h6 {
    word-break: break-all !important;
    margin: 20px 0;
    padding-bottom: 0.3em;
    line-height: 1.2;
    text-align: left;
    font-weight: bold;
    color: #2c3e50;
    position: relative; }
  .apollo h1:before,
  .apollo h2:before {
    content: "#";
    color: #42b983;
    position: absolute;
    left: -0.8em;
    top: -2px;
    font-size: 1.2em;
    font-weight: bold; }
  .apollo h1 {
    /*border-bottom: 8px solid #71BA51 !important;*/
    font-size: 20px !important; }
  .apollo h2 {
    /*border-bottom: 5px solid #71BA51 !important;*/
    font-size: 18px !important; }
  .apollo a {
    color: #42b983 !important;
    text-decoration: none !important; }
  .apollo ul,
  .apollo ol {
    padding-left: 30px; }
  .apollo li {
    line-height: 24px; }
  .apollo hr {
    height: 4px;
    padding: 0;
    margin: 16px 0;
    background-color: #e7e7e7;
    border: 0 none;
    overflow: hidden;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    border-bottom: 1px solid #ddd; }
  .apollo pre {
    background: #f2f2f2 !important;
    padding: 12px 13px; }
  .apollo code {
    margin: 0 2px;
    padding: 3px 5px;
    color: #e96900;
    border-radius: 2px;
    white-space: inherit;
    background-color: #f8f8f8; }



