.list-writing {
  color: #555;
  font-family: -apple-system-font, "Helvetica Neue", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", sans-serif; }
  .list-writing p {
    letter-spacing: 1px !important;
    line-height: 28px !important;
    min-height: 1em !important;
    -webkit-box-sizing: border-box !important;
    box-sizing: border-box !important;
    word-wrap: break-word !important;
    text-align: left;
    margin: 20px 0px !important; }
  .list-writing blockquote {
    border-left: 10px solid rgba(128, 128, 128, 0.075);
    background-color: rgba(128, 128, 128, 0.05);
    padding: 13px 15px !important;
    margin: 0px; }
  .list-writing blockquote p {
    color: #898989;
    margin: 0px; }
  .list-writing strong {
    font-weight: 700;
    color: #bf360c; }
  .list-writing body {
    font-size: 15px;
    padding: 0 30px; }
  .list-writing pre {
    background-color: #f8f8f8;
    border-radius: 3px;
    word-wrap: break-word;
    padding: 12px 13px;
    font-size: 13px;
    color: #898989; }
  .list-writing h1,
  .list-writing h2,
  .list-writing h3,
  .list-writing h4,
  .list-writing h5,
  .list-writing h6 {
    word-break: break-all !important;
    margin: 20px 0;
    line-height: 1.2;
    text-align: left;
    font-weight: bold; }
  .list-writing h1 {
    font-size: 20px !important;
    color: #bf360c;
    text-align: center; }
  .list-writing h2 {
    font-size: 18px !important; }
  .list-writing h3 {
    font-size: 16px; }
  .list-writing h4 {
    font-size: 15px; }
  .list-writing a {
    color: #4183c4 !important;
    text-decoration: none !important; }
  .list-writing ul,
  .list-writing ol {
    padding-left: 30px; }
  .list-writing li {
    line-height: 24px; }
  .list-writing hr {
    height: 4px;
    padding: 0;
    margin: 16px 0;
    background-color: #e7e7e7;
    border: 0 none;
    overflow: hidden;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    border-bottom: 1px solid #ddd; }
  .list-writing pre {
    background: #f2f2f2 !important;
    padding: 12px 13px;
    overflow: auto; }
  .list-writing code {
    color: #a71d5d;
    overflow: auto; }



